import { Col, Form, Row, Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { SettingService } from "../../../services/taskService";
import { MyFormItem } from "../../core/Input";
import { Box } from "../../core/MyBox";

export function ProjectStatusComponent(props: any) {
    const projectKey = props.projectKey;
    const type = props.type;
    // const projectKey = "c6555c5c26dae23585ac5efb0afca987";
    // const type = 1;
    const [options, setOptions] = useState([]);

    const loadData = useCallback(() => {
        SettingService.SearchByType(projectKey, type)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    var row = rs.data.map((data: any, index: number) => {
                        return {
                            key: index,
                            label: data.value,
                            value: data.id
                        }
                    })

                    setOptions(row);
                }
            })
    }, [projectKey, type]);

    useEffect(() => {
        loadData();
    }, [loadData]);
    return (
        <div>
            <MyFormItem.Select
                label={props.label}
                options={options}
                value={props.value}
                setValue={props.setValue} />
        </div>
    )
}

export function TagComponent(props: any) {
    const projectKey = props.projectKey;
    // const projectKey = "c6555c5c26dae23585ac5efb0afca987";
    const [options, setOptions] = useState([]);

    const loadData = useCallback(() => {
        SettingService.SearchByType(projectKey, 7)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    var row = rs.data.map((data: any, index: number) => {
                        return {
                            key: index,
                            label: data.value,
                            value: data.value
                        }
                    })

                    setOptions(row);
                }
            })
    }, [projectKey]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleChangeValue(value: any) {
        props.setValue(props.value !== "" ? props.value + "," + value : value);
        console.log(value);

    }

    function GenerateTag() {
        const color = ["magenta", "red", "volcano", "orange", "gold", "lime", "green", "cyan", "blue", "geekblue", "purple"];
        if (props.value === "") { return; }
        return props.value.split(",").map((data: any, index: number) => {
            return (
                <Tag onClick={() => {
                    handleDeleteTag(index);
                }} key={index} color={color[Math.floor(Math.random() * 11)]}>{data}
                </Tag>
            );
        })
    }

    function handleDeleteTag(index: number) {
        var a = props.value.split(",")
        var b = a.splice(index + 1, a.length);
        props.setValue(b.length > 0 ? b.toString(",") : "");
    }

    return (
        <Row gutter={12}>
            <Col span={12}>
                <MyFormItem.Select
                    label={props.label}
                    value={props.value}
                    setValue={handleChangeValue}
                    options={options} />
            </Col>
            <Col span={12}>
                <Box>
                    <Form.Item>
                        {GenerateTag()}
                    </Form.Item>
                </Box>
            </Col>
        </Row>
    )
}